<template>
  <div class="finace">
    <div class="title">{{ $t("finace.part1") }}: <span>{{ intoBTC }}BTC</span></div>
    <table id="financeTable" class="table pulbic-table">
      <thead>
        <tr>
          <td>{{ $t("finace.part2") }}</td>
          <td>{{ $t("finace.part3") }}</td>
          <td>{{ $t("finace.part4") }}</td>
          <td>{{ $t("finace.part5") }}</td> 
          <td>{{ $t("finace.part6") }}</td>
          <td style="width: 240px; text-align: center;" v-if="lang == 'en'">{{ $t("finace.part7") }}</td>
          <td style="width: 180px; text-align: center;" v-else>{{ $t("finace.part7") }}</td>
        </tr>
      </thead>
      <tbody>
        <!-- :src="item.currency_logo" -->
        <tr role="row" class="odd" v-for="item in selectList" :key="item.id">
          <td ><img :src="item.currency_logo" alt="coin logo"><p class="coin-name">{{ item.currency_mark }}</p></td>
          <td>{{ item.num }}</td><td>{{ item.forzen_num }}</td><td>{{ item.count }}</td><td>{{ item.convert_price }}</td>
          <td><div class=" finace-btn"><div class="" style="margin-left:0" v-if="item.is_allow_recharge == 1"><router-link :to="{path: 'transferIn', query: {'currency_id': item.currency_id}}" class="normal">{{ $t("finace.part8") }}</router-link></div>
          <div class=" " v-if=" item.is_allow_tibi == 1"><router-link :to="{path: 'transferOut', query: {'currency_id': item.currency_id}}" class="normal">{{ $t("finace.part10") }}</router-link></div>
          <div class="trade-btn" v-if="item.couple_info.length"><a href="javascript:void(0)">{{ $t("finace.part9") }}</a><ul class="second-menu2"><li v-for="list in item.couple_info" :key="list.id"><router-link tag="a" :to="{path: '/tradeDetail', query: { 'trade_area_id': list.trade_area_id, 'currency_id': list.currency_id, 'trade_currency_name': list.trade_currency_name, 'trade_currency_mark': list.trade_currency_mark }}" class="normal">{{ list.couple_name }}</router-link></li></ul></div></div></td></tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import Cookies from 'js-cookie'
  import { financeCenter } from '@/api/orderManage'

  export default {
    data() {
      return {
        selectList: [],
        intoBTC: 0,
        lang: Cookies.get('language')
      }
    },
    created() {
      this.getSelectList()
    },
    methods: {
      getSelectList() {
        financeCenter().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
          }
          this.selectList = [...res.data.normal_currency]
          this.intoBTC = res.data.convert_price
        }).catch(() => {
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .finace{
    background: white;
    padding: 20px;
    min-height: 770px;
    ul.second-menu2 {
      position: absolute;
      background: white;
      right: 0;
      box-shadow: 0 2px 6px 2px #f3f3f4;
      z-index: 3;
      width: 130px;
      overflow: hidden;
      opacity: 0;
      transition: all .15s linear;
      visibility: hidden;
  }
  ul.second-menu2 li a {
    color: #0d202b !important;
    width: 100%;
    display: inline-block;
    text-align: center;
}
    .finace-btn a{
      color: #1fa8f6;
      font-size: 12px;
    }
    .finace-btn>div{
      margin-left: 20px;
    }
    #financeTable > tbody > tr > td:last-child{
      text-align: right;
      padding-right: 20px;
    }
    .finace-btn>div{
      display: inline-block;
    }
    .trade-btn:hover .second-menu2{
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
    .trade-btn .second-menu2 a:hover{
      background: white;
    }
    .trade-btn .second-menu2{
      width: 120px;
      top: 45px;
      right: 0;
      transform: translateY(-10px);
    }
    .trade-btn {
      position: relative;
    }

    table{
      margin-top: 20px;
    }
    #financeTable tbody td img {
      max-width: 30px;
      margin-right: 5px;
      vertical-align: middle;
    }
    #financeTable tbody td p.coin-name {
      display: inline-block;
      line-height: 30px;
      vertical-align: middle;
      margin: 0;
  }
    .title{
      padding-bottom: 10px;
      margin-bottom: 10px;
      font-size: 16px;
      line-height: 30px;
      border-bottom: 1px solid #ebecf1;
      span{
        color: #1fa8f6;
      }
    }
    .pulbic-table thead tr {
        background: #f4f4f4;
        color: #64686c;
        line-height: 1.42857143;
    }
    .pulbic-table tr td:first-child {
        padding: 0px 20px !important;
    }
    .pulbic-table tr td {
        vertical-align: middle;
        text-align: left;
        line-height: 43px;
    }
    .pulbic-table {
        text-align: left;
        font-size: 12px;
    }
    table.pulbic-table tbody tr {
        border-bottom: 1px solid #f7f7f7;
    }
    .pulbic-table>tbody tr:hover{
      background: #f8f8f8;
    }
    table {
      border-spacing: 0;
      border-collapse: collapse;
      width: 100%;
      max-width: 100%;
      margin-bottom: 20px;
    }
  }
</style>

<style lang="less">
  .finace{
    .el-dialog__body{
      padding-top: 18px;
    }
  }
</style>



